@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400&family=Nunito+Sans:wght@300;400;500;600&display=swap');

* { 
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg :   rgb(35, 34, 96);
  --color-primary: white;
  --color-white: #fff;
  --color-light:rgba(255,255,255,0.6);
  --color-bg-variant:  rgb(35, 34, 96);

  --transition: all 400ms ease;

  --container-width-lg:75%;
  --container-width-md: 86%;
  --container-widt-ms:90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display:none;
}

body {
  font-family: Nunito Sans, sans-serif;
  color: var(--color-white);
  line-height: 1.7;
  background-image: url('./assets/bgimg.avif'); /* Adjust the path to your image */
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  height: 100vh; /* Add this line */

  
}

h1,h2,h3,h4,h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
  height: 100vh;
}

section > h2, section > h5 {
  text-align: center;
  color: var(--color-light);
}

section > h2 {
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: var(--color-light);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-white);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

.Title {

  margin-left: 20px; /* Adjust this value as needed */
}

.Title h3 {
  font-size: 1.5rem; /* Adjust the font size as needed */
  color: var(--color-primary);
}