.header {
  color:  rgb(35, 34, 96);
  text-align: center;
  font-size: 32px;
  margin-bottom: 18px;
  margin-top: 2rem;
}

.list-container {
 
  margin-bottom: 1.5rem;
  background-color: #a3aae3;
  text-align: center; /* Add this line to center the text */
}
  
.list-container input::placeholder {
  text-align: center;
}

.custom-row {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.link {
  text-decoration: none;
  color: #333;
  font-size: 16px;
}

.link:hover {
  color: #555;
}

.container {
  max-width: 500px;
  margin: 5rem auto; /* Adjusted margin to move the box up */
  background-color: white; /* Set a background color */
  padding: 1px;
  text-align: center; /* Center the content */
}

.headings {
  padding: 2rem;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.btn-primary {
  width: 100%;
  font-size: 1.25rem; 
  padding: 0.75rem; 
  text-align: center;
  background-color:  rgb(35, 34, 96);
}

.flex-container {
  border: none;
  background-color: transparent;
  font-size: 1.25rem;
  margin-bottom: 1.5rem;
  height: 3rem;
  width: 100%;
  padding-top: 0.75rem; /* Adjusted padding */
  padding-bottom: 0.75rem; /* Added padding */
  box-sizing: border-box;
  line-height: 1.5; /* Added line height */
}

/* Style for email and password input fields */
.list-container input {
  border: none; /* Remove the border */
  background-color: transparent; /* Set a transparent background */
  color: rgb(35, 34, 96); /* Set the text color to teal */
  font-size: 1.25rem; /* Adjust font size as needed */
  margin-bottom: 1.5rem; /* Add some margin */
  height: 3rem; /* Set the height of the input fields */
  width: 100%; /* Set the width to 100% */
  padding-top: 1rem; /* Adjust top padding to center text vertically */
  box-sizing: border-box; /* Ensure padding is included in the height */
  caret-color: rgb(35, 34, 96);
}

/* Remove the default outline when input fields are selected */
.list-container input:focus {
  outline: none;
  padding: 0.5rem;
}
.signup-button {
  background-color:rgb(35, 34, 96); /* Set the background color to blue */
  color: white; /* Set the text color to white */
  border: none; /* Remove border if any */
}

.signup-button:hover {
  background-color: rgb(93, 92, 153); /* Keep the background color on hover */
  color: white; /* Keep the text color on hover */
}

.remember-me {
  color:rgb(35, 34, 96); /* Set the color to blue */
}
.signup-button:active {
  transform: translateY(2px); /* Adjust the value to your preference */
}
