.PFheader {
  color: white;
  text-align: center;
  font-size:47px;
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.form-container {
  max-width: 500px;
  margin: 0rem auto; /* Increased margin to push the box further down */
  height: 40vh;
  background-color: white;
  padding: 1px;
}

.label {
  margin-left: 30px;
  display: block;
  font-weight: bold;
  margin-bottom: 8px;
  margin-top: 20px;
  color: rgb(35, 34, 96);
}

.input {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
}

.result {
  font-size: 20px;
  color: rgb(35, 34, 96);
}

.input:focus {
  outline: none;
  border: 2px solid rgb(35, 34, 96); /* Add a border on focus */
}

.PFintro {
  text-align: center;
  font-size: 18px;
  line-height: 1.6;
  padding: 40px 20%;
  color: white;
}
