.Iheader {
  color: white;
  text-align: center;
  font-size: 32px;
  margin-bottom: 18px;
  margin-top: 2rem;
}

.list-container {
  max-width: 800px;
  margin: 0 auto;
}

.list-item {
  margin-bottom: 10px;
}

.card-img-top {
  max-width: 100%;
  height: auto;
  max-height: 100px;
}

.card-body {
  text-align: center;
  color: white;
}

.card-title a {
  color: white;
  font-size: 1.25rem;
  margin-bottom: 0.75rem;
  transition: color 0.3s ease;
}

.card-title a:hover {
  color: rgb(186, 186, 192);
}


.card-text {
  font-size: 1rem;
  color: white;
}

.scholarshipList {
  color: white;
  list-style: none;
  padding: 0;
}

.scholarshipList li {
  margin-bottom: 20px;
}

.scholarshipList li p {
  margin-bottom: 0;
}

.scholarshipList li:last-child {
  margin-bottom: 0;
}
.grid-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 30px;
}

.card {
  margin-bottom: 20px;
  width: 18rem;
  margin-bottom: 20px;
}
.intro {
  text-align: center;
  font-size: 18px;
  line-height: 1.6;
  padding: 60px 20%;
  color: white;
}

.card-img-top.square-logo {
  max-width: 100px;
  height: 100px; /* Set a fixed height */
  object-fit: cover;
  
}
/* Styles for logos */
.logo-container {
  display: flex; /* Use flexbox to center elements */
  justify-content: center; /* Center items horizontally */
  margin-bottom: 10px; /* Add margin between logo and card body */
}


