.nav {
    background-color:  white;
    color: rgb(35, 34, 96);
    font-weight: 600;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 2rem;
    padding: 0 3rem;
  }
  .nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
  }
  .nav a {
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
  }
  
  .nav li.active {
    background-color:  rgb(151, 150, 194);
  }
  
  .nav li:hover {
    background-color:#54597b;
  }